import React from "react";
import Red_Layer from "../layer/Red_Layer";
import draw_canvasUI from "./draw_canvasUI";

const SIZE = 100;

class Red_Canvas extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      canvasMoveState: false,
      canvasViewOffsetX: 0,
      canvasViewOffsetY: 0,
      canvasViewScale: 1,
      canvasBgColorPickerOpenYn: false
    };
  }

  draw_canvasUI = draw_canvasUI;

  drawCall(canvasInfo, layers, bgColor) {
    return <div style={{
      ...style.canvas,
      transform: `translate(calc(-50% + ${this.state.canvasViewOffsetX}px),calc(-50% + ${this.state.canvasViewOffsetY}px)) scale(${this.state.canvasViewScale})`
    }} className={'transparent_checker'}>
      <div
        className={'transparent_checker'}
        style={{
          width: `${canvasInfo.width}px`, height: `${canvasInfo.height}px`,
          background: Red_Layer.calcGradients(layers, true, bgColor),
          transition: 'width 0.2s, height 0.2s'
        }}
      />

    </div>;
  }

  render() {
    const rootComponent = this.props.rootComponent;
    const rootComponentState = rootComponent.state;
    const canvasInfo = rootComponentState.canvasInfo;
    const layers = rootComponentState.layers;
    const activeLayer = rootComponentState.activeLayer;
    const activeSubData = rootComponentState.activeSubData;
    const layerSize = activeLayer['size'];
    return <div
      style={style.container}
      onMouseMove={e => {
        if (this.state.useMove) {
          e = e.nativeEvent;
          this.state.canvasViewOffsetX += e.movementX;
          this.state.canvasViewOffsetY += e.movementY;
          style.canvas.transition = '';
          this.setState({});
          document.body.style.cursor = 'move';
          console.log(e);
        }
      }}
      onMouseLeave={e => this.state.useMove ? this.setState({useMove: false}) : 0}
      onMouseUp={e => this.state.useMove ? (this.setState({useMove: false}), document.body.style.cursor = 'default') : 0}
      onMouseDown={e => e.nativeEvent.button === 1 ? this.setState({useMove: true}) : 0}
      onWheel={e => {
        let t0 = this.state.canvasViewScale - e.nativeEvent.deltaY / 1000;
        if (t0 < 0) t0 = 0.01;
        this.setState({canvasViewScale: t0});
        style.canvas.transition = 'transform 0.1s';
      }}
    >
      {this.draw_canvasUI()}
      {this.drawCall(canvasInfo, layers, rootComponentState.bgColor)}
    </div>;
  }
}

export default Red_Canvas;
const style = {
  container: {
    position: 'absolute',
    top: 0, left: 0, right: 0, bottom: 0,
    overflow: 'hidden'
  },
  canvas: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    overflow: 'auto',
    transition: 'transform 0.01s'
  },
  itemContainer: {
    whiteSpace: 'nowrap'
  }
};
